@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

:root {
  --primary: #48c;
  --danger: #c46;
  --success: #4a8;
  --secondary: #666;
  --dark: #333;
  --light: #eee;
  --link: #4bc;
  --warning: #da2;
}

* {
  font-family: 'Roboto', sans-serif;
}

input {
  font-family: 'Roboto', sans-serif;
  font-size: 1em;
}

body {
  margin: 0;
  background-color: var(--primary);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

/* BUTTONS */

.btn {
  cursor: pointer;
  outline: 0;
  border: 0;

  padding: 0.5em 1em;
  border-radius: 0.5em;

  transition: filter 0.2s ease;
  font-size: 1.2em;

  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.3);
}

.btn:hover {
  filter: brightness(1.2);
}

.btn-warning {
  background-color: var(--warning);
  color: var(--dark);
}

.btn-success {
  background-color: var(--success);
  color: var(--light);
}

/* APP DIVS */

.app-content {
  margin: 2em;
  display: flex;
  justify-content: center;
}

/* RESULTS */

.app-resultados {
  margin: 2em;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1em;
}

.resultados-wrapper {
  display: flex;
  flex-direction: column;
  gap: 0.5em;
}

.resultado {
  background-color: var(--dark);
  color: var(--light);
  padding: 0.5em 1em;
  font-size: 1.2em;

  display: flex;
  justify-content: space-between;
  gap: 1em;
  flex-wrap: wrap;

  border-radius: 0.5em;
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.3);
}

.rr-rating {
  display: flex;
  flex-direction: column;
  gap: 1em;
  width: 300px;
}

.rr-rating-title {
  background-color: var(--success);
  color: var(--light);
  padding: 1em;
  font-weight: 700;
  text-align: center;

  border-radius: 0.5em;
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.3);
}

.rr-rating-desc {
  background-color: var(--light);
  color: var(--dark);
  padding: 1em;

  border-radius: 0.5em;
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.3);
}

/* CONTROLS */

.app-controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2em;
  margin: 2em;
}

.app-controls-inputs {
  background-color: var(--warning);
  padding: 1em;
  border-radius: 0.5em;
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.3);

  display: flex;
  justify-content: center;
  gap: 0.5em;
}

.control-input {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.5em;
}

.control-input input {
  background: 0;
  border: 0;
  outline: 0;
  border-bottom: 2px solid var(--dark);
}

.app-controls-buttons {
  display: flex;
  justify-content: center;
  gap: 0.5em;
}

/* TABLE */

.main-table {
  border-collapse: collapse;
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.3);
}

.main-table td {
  text-align: center;
  white-space: nowrap;
  padding: 0;
  margin: 2em;
}

.table-label {
  background-color: var(--dark);
  color: var(--light);
}

.table-label2 {
  background-color: var(--secondary);
  color: var(--light);
}

.table-td-operador {
  border-top: 2px solid var(--secondary);
}

.table-td-operador div,
.table-td-equipo div {
  padding: 0.5rem;
}

.table-td-ensayo div {
  display: flex;
}

.table-td-ensayo div input {
  flex: 1;
  padding: 0.5em;
  border: 1px solid rgba(0, 0, 0, 0.3);
}
